<template>
  <div class="container">
    <!-- 头部导航栏 -->
    <navbar class="navbar" @click.native="tosearch">
      <i class="iconfont icon-logo1" slot="back"></i>
      <van-search v-model="value" placeholder="请输入搜索关键词" slot="title" />
      <div slot="info" class="iconfont icon-fabu"></div>
    </navbar>

    <!-- 粘性布局 -->
    <van-sticky :offset-top="0">
      <!--  Tab栏-->
      <van-tabs v-model="active" id="tab_menu" swipeable>
        <van-tab
          v-for="(item, index) in categories"
          :key="index"
          v-if="item.is_top == 1"
          :title="item.name "
        ></van-tab>
        <router-link to="/cloumn" class="iconfont icon-gengduo"></router-link>
      </van-tabs>
    </van-sticky>
    <!-- 下拉刷新 -->
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
      <div class="mp-30"></div>
      <!-- keep-alive 保存上一页的状态 -->
      <keep-alive>
        <!-- 新闻列表组件 -->
        <listsitem :news="categories[this.active].list" />
      </keep-alive>
      <!-- 分页加载 -->
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="----我是有底线的----"
        @load="onLoad"
        :immediate-check="false"
      ></van-list>
    </van-pull-refresh>
    <!-- 底部工具栏 -->
    <toolbar>
      <div slot="home">
        <a href="#">
          <span class="iconfont icon-remen" style="color: #ed4044;"></span>
          <p style="color: #da4938;">头条</p>
        </a>
      </div>
    </toolbar>
  </div>
</template>

<script>
import toolbar from "@/components/toolbar.vue";
import navbar from "@/components/navbar.vue";
import listsitem from "@/components/lists-item.vue";

export default {
  name: "index",
  // activated 只对keep-alive的组件有用，当组件每次加载的时候会执行
  activated() {
    // 当栏目管理页面执行完之后
    const column = JSON.parse(localStorage.getItem("column"));
    const { token } = JSON.parse(localStorage.getItem("user_info")) || "";
    this.categories = column;
    // 渲染新闻信息
    this.getlists();
  },
  mounted() {
    // 1.判断本地存储是否有数据，有则将数据保存到this.categories，没有则法送网络请求
    // 2.判断是否已登录，获取token
    // 3.切换tab页签，根据不同的tab id获取数据
    const column = JSON.parse(localStorage.getItem("column"));
    const { token } = JSON.parse(localStorage.getItem("user_info")) || "";
    this.token = token;
    // 已登录状态
    if (token) {
      if (!column) {
        this.getListItem(token, column);
      } else if (column[0].name == "关注") {
        this.categories = column;
        // 渲染新闻信息
        this.getlists();
      } else if (column && column[0].name !== "关注") {
        // 获取栏目信息
        this.getListItem(token, column);
      }
    } else {
      // 获取栏目列表(未登录)
      this.getListItem(token, column);
    }
  },

  data() {
    return {
      loading: false,
      finished: false,
      refreshing: false,
      pageIndex: 1,
      token: "",
      value: "",
      active: 0,
      // 栏目
      categories: [{}],
      // 新闻列表
      news: []
    };
  },
  watch: {
    // 监听tab栏的属性变化，触发获取数据
    active() {
      // 获取新闻列表数据
      this.getlists();
    }
  },
  methods: {
    tosearch() {
      this.$router.push("/search");
    },
    // 给每个栏目添加 自己的页数、文章列表数组、loading(文章是否正在加载)、finished(是否加载完成)
    handleCategories() {
      this.categories = this.categories.map(item => {
        item.pageIndex = 1;
        item.list = [];
        item.finished = false;
        item.loading = false;
        item.scrollY = 0;
        return item;
      });
      // 将栏目类名保存到本地
      localStorage.setItem("column", JSON.stringify(this.categories));
    },

    // 获取新闻列表函数封装
    getlists() {
      const { id, pageIndex, finished, list } = this.categories[this.active];

      console.log("栏目" + id, pageIndex + "页");

      // 如果当前的栏目数据已经加载完毕了，否则会重复累加数据
      if (this.categories[this.active].finished) {
        return;
      }

      this.$axios({
        url: "/post",
        methods: "get",
        params: {
          pageIndex: pageIndex,
          pageSize: 5,
          category: id
        },
        headers: {
          // 判断是否有tonken
          Authorization: this.token || ""
        }
      })
        .then(res => {
          const { data, total } = res.data;
          // 将刷新的数据合并到当前
          this.categories[this.active].list = [
            ...this.categories[this.active].list,
            ...data
          ];
          // 用赋值的方式触发页面刷新
          this.categories = [...this.categories];

          // 如果当前文章的条数等于total总条数，说明数据已经加载完毕
          if (this.categories[this.active].list.length >= total - 1) {
            this.finished = true;
            this.categories[this.active].finished = true;
            // 告诉组件当前的请求加载完毕
            this.loading = false;
            this.categories[this.active].loading = false;
            return;
          }
          // 加载状态为结束
          this.loading = false;
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 获取tab栏的类目
    getListItem(token, column) {
      if (token || (token && column)) {
        this.$axios({
          url: "/category",
          methods: "get",
          headers: {
            Authorization: token
          }
        })
          .then(res => {
            // 将栏目信息保存到本地
            this.categories = res.data.data;
            // 给每个栏目添加pageindex
            this.handleCategories();
            // 获取新闻列表
            this.getlists();
          })
          .catch(error => {
            console.log(error);
          });
      }

      // 未登录状态
      else {
        this.$axios({
          url: "/category",
          methods: "get"
        })
          .then(res => {
            console.log("未登录");
            // 将栏目信息保存到本地
            this.categories = res.data.data;
            // 给每个栏目添加pageindex
            this.handleCategories();
            // 获取新闻列表
            this.getlists();
          })
          .catch(error => {
            console.log(error);
          });
      }
    },

    onLoad() {
      this.categories[this.active].pageIndex++;
      this.getlists();
    },
    // 下拉刷新
    onRefresh() {
      this.refreshing = false;
    }
  },
  components: {
    toolbar,
    navbar,
    listsitem
  }
};
</script>

